@import url(https://fonts.googleapis.com/css2?family=Abril+Fatface&family=DM+Serif+Text&family=Josefin+Slab:wght@300&family=Poiret+One&family=Vidaloka&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.slider{margin-top:-2%;width:100%;height:100vh;box-sizing:border-box;border-radius:2em;padding:0;display:flex;align-items:center;position:relative;overflow:hidden}.slider i{font-size:2vw}.slide{min-width:100%;height:80%;position:relative;transition:0.5s;overflow:hidden}#goLeft,#goRight{position:absolute;top:50%;transform:translateY(-50%);width:10%;height:80%;background:none;border:none;outline:none;transition:0.5s}#goLeft:hover,#goRight:hover{background:rgba(0,0,0,0.267);cursor:pointer}#goLeft:hover i,#goRight:hover i{color:rgba(245,245,245,0.66)}#goLeft{left:0}#goRight{right:0}

