.pbox{
    background: white;
    padding: 2em;
    position: relative;
    box-shadow: 0 25px 25px -35px rgba(0, 0, 0, 0.15);
}
.pbox::before,
.pbox::after{
    content: '';
    position: absolute;
    top:90%;
    bottom: 20px;
    box-shadow: 0 16px 15px rgba(0, 0, 0, 0.35);
    z-index: -1;
}

.pbox::before{
    background: lightblue;
    left: 5px;
    right: 45px;
    transform: rotate(-6deg);
}
.pbox::after{
    background:pink;
    left: 45px;
    right: 5px;
    transform: rotate(6deg);

}

.box {
    margin-top: 10%;
